import {eventsData} from 'services/eventsData';
import moment from 'moment';

export class DataRepository {
  getEvents() {
    var promise = new Promise(
      (resolve, reject) => {
        if (!this.events) {
          setTimeout(
            () => {
              this.events = eventsData;
              this.events.forEach(
                (item) => {
                  var dateTime = moment(item.dateTime).format("MM/DD/YYY HH:mm");
                  item.dateTime = dateTime;
                });
              resolve(this.events);
            }, 2000);
        } else {
          resolve(this.events);
        }
      });
    return promise;
  }

  getEvent(eventId) {
    return this.events.find((item) => {return item.id == eventId});
  }
}
