export class App {

  constructor() {

  }

  configureRouter(config, router) {
    this.router = router;
    config.title = "Aurelia Pluralsight Course";
    config.map([
      {route: ['', 'events'],         moduleId: 'events/events',          title: 'Events',            name: 'Events',     nav: true},
      {route: 'jobs',                 moduleId: 'jobs/jobs',              title: 'Jobs',              name: 'Jobs',       nav: true},
      {route: 'discussion',           moduleId: 'discussion/discussion',  title: 'Discussion',        name: 'Discussion', nav: true},
      {route: 'eventDetail/:eventId', moduleId: 'events/eventDetail',                                 name: "EventDetail"},
    ])
  }
}
